interface BriefingHeaderProps {
  id: string
  title: string
  objective?: string
  className?: string
}

/**
 * BriefingHeader — strict section opener.
 * Shows a monospace section ID, serif title, and one-line objective.
 */
export default function BriefingHeader({ id, title, objective, className = '' }: BriefingHeaderProps) {
  return (
    <div className={`briefing-header ${className}`}>
      <span className="briefing-id">{id}</span>
      <h2 className="briefing-title">{title}</h2>
      {objective && <p className="briefing-objective">{objective}</p>}
    </div>
  )
}
